import React from 'react';
import MinhaFoto from "../../img/img2.jpeg";
import "./AboutMe.css";




class SobreMim extends React.Component{
    render(){
        return(

            <div className="sobre-mim-container">
                <div className="foto-container">
                    <img className="foto-perfil" src={MinhaFoto} alt="Foto de perfil de Jess"></img>
                </div>
                    

                <div className="apresentacao">
                    <h3 className="titulo">Descubra um pouco sobre mim</h3>
                    <p className="descricao">
                        Olá! 
                        Sou estudante de sistemas de informação pela Universidade Federal 
                        Rural de Pernambuco desde 2018. Estou aprendendo desenvolvimento web e desenvolvimento
                        android. Amo assistir séries, cozinhar e sair com os meus amigos.
                        Tenho uma paixãozinha por poemas, e algumas vezes gosto de escrever alguns.
                    </p>
                </div>
            </div>
        )
    }
}

export default SobreMim;
