import React from 'react';
import "../contato/Contato.css";
import AnchorLink from "react-anchor-link-smooth-scroll";
import linkedin from "../../img/linkedin.png";
import fb from "../../img/fb-icon.png"
import git from "../../img/git.png"
import insta from "../../img/insta.png"





class Contato extends React.Component{
    render(){
        return(
            <div className="contato-container">
                <h3 className="contato">Contato</h3>
                <div className="icons-container">
                    <div className="social">
                        <h4 className="social-medias">
                            Localização
                        </h4>
                        <h5 className="email">
                            Recife, Pernambuco
                        </h5>
                    </div>

                    <div className="social">
                        <h4 className="social-medias">
                            E-mail
                        </h4>
                        <h5 className="email">
                            al.souza.jessica@gmail.com
                        </h5>
                    </div>

                        

                    <div className="social">
                        <h4 className="social-medias">
                            Telefone
                        </h4>
                        <h5 className="email">
                            (81) 9.9656 - 6759
                        </h5>
                    </div>
                    </div>
                    <div className="social">
                        <h4 className="social-medias">Redes Sociais</h4>

                        <div className="icons-container">
                            <AnchorLink href='//google.com'>             
                                <img  className="icons" src={linkedin} alt="icone do linkedin"></img>
                            </AnchorLink>
                            <AnchorLink href='//google.com'>             
                                <img  className="icons" src={fb} alt="icone do linkedin"></img>
                            </AnchorLink>
                            <AnchorLink href='//google.com'>             
                                <img  className="icons" src={git} alt="icone do linkedin"></img>
                            </AnchorLink>
                            <AnchorLink href='//google.com'>             
                                <img  className="icons" src={insta} alt="icone do linkedin"></img>
                            </AnchorLink>
                        </div>
                    </div>
                    

            </div>
        )
    }
}

export default Contato;
