import React from 'react';
import "./inicial.css";
import AnchorLink from "react-anchor-link-smooth-scroll";
import Setinha from "../../img/setinha.png";



class Inicial extends React.Component{
    render(){
        return(

            <div className="inicial-container">
                <div>
                    <h3 className="aboutme">Eu sou Jéssica Alves</h3>
                    <h1 className="boasvindas">Seja bem vindo(a)!</h1>
                    <h3 className="botao">
                        Venha me conhecer!
                    </h3>
                    <AnchorLink href='#aboutme'>
                        <img className="setinha" src={Setinha} alt="Seta referenciando sentido sul"></img>
                    </AnchorLink>
                    
                </div>
            </div>
                
        )
    }
}

export default Inicial;
