import React from 'react';
import NavbarItem from "./Navbaritem";
import "./NavBar.css";
import AnchorLink from "react-anchor-link-smooth-scroll";


class Navbar extends React.Component{
    render(){
        return(
            <div className="navbar">
                <AnchorLink className='estoutriste' href='#inicio'>
                    <NavbarItem title="Inicio"></NavbarItem>
                </AnchorLink>
                <AnchorLink className='estoutriste' href='#aboutme'>
                    <NavbarItem title="Sobre mim"></NavbarItem>
                </AnchorLink>
                <AnchorLink className='estoutriste' href='#habilidades'>
                    <NavbarItem title="Conhecimentos"></NavbarItem>
                </AnchorLink>
                <AnchorLink className='estoutriste' href='#project'>
                    <NavbarItem title="Projetos"></NavbarItem>
                </AnchorLink>
                <AnchorLink className='estoutriste' href='#contato'>
                <NavbarItem title="Contato"></NavbarItem>
                </AnchorLink>
            </div>
        )
    }
}

export default Navbar;